/* Importa un font da Google Fonts */
@import url('https: //fonts.googleapis.com/css2?family=IBM+Plex+Sans+Devanagari:wght@100;200;300;400;500;600;700&display=swap" rel="stylesheet');

body, html {
  margin: 0;
  padding: 0;
  font-family: IBM Plex Sans Devanagari,sans-serif!important;
  
  
}

h1 {
  font-size: 2.5em; /* Aumenta la dimensione del titolo */
  margin: 0.67em 0; /* Aggiunge spazio sopra e sotto il titolo */
}

h2 {
  font-size: 2em;
  margin: 0.75em 0;
  font-weight: 700; /* Rende il titolo grassetto */
}

p {
  font-size: 1em;
  line-height: 1.5; /* Aumenta l'interlinea */
  margin-bottom: 1em; /* Aggiunge spazio sotto i paragrafi */
}

.home-page {
  margin: 15px;
  padding: 50px 50px 50px 50px;
  background-color: white; /* Imposta lo sfondo bianco per la sezione home-page */
}

/* Stili per l'header comune a tutte le pagine */
.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 15px;
  border-radius: 15px;
  background-image: url('/home/lupol/front-dao-ipfs/src/icona-1.webp'); /* Percorso dell'immagine di sfondo WebP */
  background-size: cover; /* Copre l'intera sezione, mantenendo le proporzioni */
  background-position: center; /* Centra l'immagine di sfondo */
}

.header-content {
  width: 40%; /* Larghezza specifica */
  height: 400px; /* Altezza specifica */
  box-shadow: 0 2px 8px #0b0e1129;
  background-color: #f0f0f0; /* Colore di sfondo */
  padding: 0px 0px; /* Spazio intorno al contenuto */
  margin: 0 20px; /* Margini laterali per separare dagli altri elementi */
  border-radius: 20px; /* Bordi arrotondati */
  display: flex; /* Abilita Flexbox */
  justify-content: center; /* Centra il contenuto orizzontalmente */
  align-items: center; /* Centra il contenuto verticalmente */
  flex-direction: column; /* Organizza i figli in colonna */
}

/* Stili per il logo nell'header */
.header-logo {
opacity: 0.5; /* Imposta l'opacità al 50% */
max-width: 40%;
border-radius: 20px; /* Adatta questo valore per avere l'arrotondamento desiderato */
}

/* Stili per la barra di navigazione */
nav ul {
list-style-type: none;
margin: 0;
padding: 0;
background-color: #333;
overflow: hidden;
}

nav li {
float: left;
}

nav li a {
display: block;
color: white;
text-align: center;
padding: 14px 16px;
text-decoration: none;
}

nav li a:hover {
background-color: #ddd;
color: black;
}

/* Stili per il footer */
footer {
background-color: #131212;
color: white;
text-align: center;
padding: 10px 0;
position: fixed;
bottom: 0;
width: 100%;
}

.features-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px; /* Sottrai il margine esterno per allineare ai bordi esterni */
  margin-bottom: 60px;
}

.feature {
  width: calc(33.333% - 20px); /* Sottrai il doppio del margine per mantenere la distanza */
  margin: 10px;
  border: 2px solid #333;
  border-radius: 25px;
  padding: 20px;
  box-sizing: border-box; /* Include padding e bordo nella larghezza */
}

@media (max-width: 600px) {
  .feature {
    width: calc(50% - 20px); /* Aumenta la larghezza per viewport più piccole */
  }
}
  .feature {
    margin: 10px; /* Aumenta o regola il margine se necessario */
  }


.feature img {
  width: 100%;
  max-width: 200px; /* Adatta questa dimensione in base al layout desiderato */
  border-radius: 10px; /* Bordi arrotondati per le immagini */
  margin-bottom: 10px;
}

.feature h2 {
  margin: 10px 0;
}


.feature p {
  margin: 10;
}

